.button {
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 10px;
	padding: 15px 20px;
	color: #FFF;
	font-size: 15px;
	transition: all 0.3s ease-in-out;
	&._orange {
		background: #ff6b35;
	}
	&._arrow {
		justify-content: space-between;
		gap: 15px;
	}
	&._maxwdth-220{
		max-width: 220px;
	}
	&__icon {
		background-color: #fff;
		width: 25px;
		height: 25px;
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 50%;
		color: #000;
	}
}
.title{
	color: #000;
	font-weight: 400;
	line-height: normal;
	@include adaptiveValue("font-size", 52, 22);
	@include adaptiveValue("margin-bottom", 70, 32);
	&._orange-span span{
		color: #FF6B35;
	}
}
.detail-link{
	display: inline-flex;
	align-items: center;
	gap: 9px;
	color: #00000050;
	font-size: 15px;
	line-height: normal;
	transition: all 0.3s ease-in-out;
	&:hover{
		color: #000000;
	}
}
.grid-3col{
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 58px;
	@media (max-width: 1280px) {
		grid-gap: 30px;
	}
}
.small-item{
	&._shadowed{
		border-radius: 10px;
		background: #FFF;
		box-shadow: 5px 5px 35px 0px rgba(0, 0, 0, 0.10);
	}
}