.hero {
	position: relative;
	&__slide.swiper-slide-active{
		z-index: 1;
	}
	&__slide-wrapper{
		position: relative;
		@include adaptiveValue("height", 780, 600);
	}
	&__slide-container{
		width: 100%;
		height: 100%;
		max-width: 630px;
		position: relative;
		z-index: 1;
		color: #fff;
		display: flex;
		flex-direction: column;
		// justify-content: center;
		align-items: flex-start;
		@include adaptiveValue("padding-top", 150, 80);
	}
	&__slide-image {
		position: absolute;
		@include adaptiveValue("right", -175, -100);
		bottom: 0;
		img{
			width: 100%;
			height: 100%;
			object-fit: cover
		}
	}
	&__slide-title {
		@include adaptiveValue("font-size", 62, 32);
		margin-bottom: 18px;
		line-height: normal;
	}
	&__slide-subtitle {
		@include adaptiveValue("font-size", 20, 16);
		margin-bottom: 50px;
		line-height: 1.6;
	}
	@media (max-width: $mobile) {
		&__slide-image {
			height: 320px;
			img{
				width: auto;
				height: 100%;
				object-fit: cover
			}
		}
	}
}
.swiper-pagination {
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 11px;
	position: absolute;
	@include adaptiveValue("bottom", 80, 60);
	width: 100%;
	z-index: 2;
	.swiper-pagination-bullet {
		width: 7px;
		height: 7px;
		border-radius: 3.5px;
		background-color: rgba(255, 255, 255, 0.5);
		transition: width 0.3s ease;
	}
	.swiper-pagination-bullet-active {
		background-color: rgba(255, 255, 255, 1);
		width: 75px;
	}
}
.offers{
	margin-top: 80px;
	margin-bottom: 110px;
	&__item {
		position: relative;
		padding: 55px 30px 60px;
		min-height: 490px;
		display: flex;
		flex-direction: column;
		gap: 25px;
	}
	&__item-title {
		font-size: 32px;
		line-height: normal;
	}
	&__item-list {
		position: relative;
		z-index: 1;
	}
	&__item-link {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		border: 1px solid #DEDEDE;
		border-radius: 32px;
		background-color: #fff;
		transition: all 0.3s ease-in-out;
		padding: 10px 15px;
		margin-bottom: 16px;
		font-size: 15px;
		&:hover{
			background-color: #FF6B35;
			color: #fff;
			border: 1px solid #FF6B35;
		}
	}
	&__item-image {
		position: absolute;
		bottom: 20px;
		right: 20px;
		img{
			width: 100%;
			height: auto;
		}
	}
	&__detail{
		margin-top: auto;
	}
	@media (max-width: 1280px) {
		&__item-title {
			font-size: 28px;
		}
		&__item-link {
			border-radius: 10px;
			font-size: 13px;
		}
		&__item-image {
			bottom: 15px;
			right: 15px;
			max-width: 40%;
		}
	}
}