.header {
	&._bg-1 {
		background: url('@img/backgrounds/header-1.png') center center no-repeat;
		background-size: cover;
	}
	&__grid {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: space-between;
		@include adaptiveValue("padding-top", 40, 20);
		@include adaptiveValue("padding-bottom", 40, 20);
		@include adaptiveValue("gap", 30, 20);
		&::after{
			content: "";
			display: block;
			position: absolute;
			width: 100%;
			height: 1px;
			background-color: rgba(255, 255, 255, 0.50);
			bottom: 0;
			left: 0;
		}
	}
	&__grid-column {
		&._logo{
			flex: 0 0 auto;
			@include adaptiveValue("width", 172, 127);
			@include adaptiveValue("margin-right", 40, 10);
		}
		&._menu{
			flex: 1 0 570px;
			max-width: 770px;
		}
		&._tools{
			flex: 0 1 360px;
		}
	}
	&__logo {
		width: 100%;
		img{
			width: 100%;
			height: auto;
		}
	}
	&__tools {
		display: flex;
		align-items: center;
		@include adaptiveValue("gap", 20, 10);
	}
	&__phone {
		color: #FFF;
		font-size: 18px;
		font-weight: 700;
		line-height: 1.77;
		margin-right: auto;
		img{
			display: none;
		}
	}
	&__profile, &__message {
		@include adaptiveValue("width", 50, 35);
		@include adaptiveValue("height", 50, 35);
		flex-shrink: 0;
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	&__profile {
		background-color: #fff;
	}
	&__message {
		background-color: #FF6B35;
	}
	@media (max-width: 1530px) {
		&__phone {
			margin-right: 0;
			margin-left: auto;
		}
	}
	@media (max-width: 1120px) {
		&__grid-column {
			&._logo{
				flex: 0 0 auto;
				@include adaptiveValue("width", 172, 127);
				@include adaptiveValue("margin-right", 40, 10);
			}
		}
		&__phone {
			display: flex;
			align-items: center;
			justify-content: center;
			@include adaptiveValue("width", 50, 35);
			@include adaptiveValue("height", 50, 35);
			border: 1px solid #fff;
			border-radius: 50%;
			img{
				display: block;
			}
			span{
				display: none;
			}
		}
	}
	@media (max-width: $tablet) {
		&__grid-column {
			justify-content: flex-start;
			&._logo{
				margin-right: auto;
				margin-left: 10px;
			}
			&._menu{
				flex: 0 0 40px;
				order: -1;
			}
		}
	}
	@media (max-width: $mobile) {
		&__grid-column {
			&._logo{
				margin-left: 0;
			}
			&._menu{
				flex: 0 0 30px;
			}
		}
	}
}